.wrapper {
  @apply absolute top-0 bottom-0 right-0 left-0 m-auto flex flex-col items-center justify-center text-center font-semibold text-4xl;

  @media (max-width: 480px) {
    @apply text-xl px-3;
  }

  .logo {
    @apply bg-[url('../../img/logo.png')] flex items-center w-[150px] h-[150px] bg-no-repeat bg-contain backdrop-filter;
  }
}
